import 'bootstrap/dist/css/bootstrap.min.css'
import "../Styles/MainStyle.css"
import "../Styles/tarifs.css"
import {useNavigate} from 'react-router-dom'
import { useState } from 'react';

function Tarifs() {
    const [isActive1, setIsActive1] = useState(false);
    const [isActive2, setIsActive2] = useState(false);
    const [isActive3, setIsActive3] = useState(false);

    const activeClass = "card-button active";
    const unactiveClass = "card-button unactive";

    const navigate = useNavigate();
  
    const OpenForm = () =>{
      navigate("/form");
    }

  return (
    <div class="tarifs" id='tarifs'>
        <div class="container">
            <div class="tarif-title">
                
            </div>
            <div class="row">
                <div class="col-md-4 card" onMouseEnter={() => {setIsActive1(true)}} onMouseLeave={() => setIsActive1(false)}>
                    <div class="card-content">
                        <div class="card-title">
                            
                        </div>
                        <div class="card-price">
                            2000<span class="sign"> </span>     
                        </div>
                        <div class="card-price-time">
                             
                        </div>
                        <div class="card-list-content">
                            <ul class="card-list">
                                <li class="card-list-item">
                                      2 
                                </li>
                                <li class="card-list-item">
                                        SEO
                                </li>
                                <li class="card-list-item">
                                     
                                </li>
                                <li class="card-list-item">
                                      
                                </li>
                                <li class="card-list-item">
                                          
                                </li>
                            </ul>
                        </div>
                        <button onClick={() => OpenForm()} class="card-button" className={isActive1 && activeClass || unactiveClass}> !</button>
                    </div>
                </div>
                <div class="col-md-4 card" onMouseEnter={() => {setIsActive2(true)}} onMouseLeave={() => setIsActive2(false)}>
                    <div class="card-content">
                        <div class="card-title">
                            
                        </div>
                        <div class="card-price">
                            2000<span class="sign"> </span>     
                        </div>
                        <div class="card-price-time">
                             
                        </div>
                        <div class="card-list-content">
                            <ul class="card-list">
                                <li class="card-list-item">  10 </li>
                                <li class="card-list-item">    SEO</li>
                                <li class="card-list-item"> </li>
                                <li class="card-list-item">   <br/> 2  </li>
                                <li class="card-list-item">   </li>
                            </ul>
                        </div>
                        <button onClick={() => OpenForm()} className={isActive2 && activeClass || unactiveClass}> !</button>
                    </div>
                </div>
                <div class="col-md-4 card" onMouseEnter={() => {setIsActive3(true)}} onMouseLeave={() => setIsActive3(false)}>
                    <div class="card-content">
                        <div class="card-title">
                            VIP
                        </div>
                        <div class="card-price">
                            1800<span class="sign"> </span>     
                        </div>
                        <div class="card-price-time">
                             
                        </div>
                        <div class="card-list-content">
                            <ul class="card-list">
                                <li class="card-list-item">  100 </li>
                                <li class="card-list-item">    SEO</li>
                                <li class="card-list-item"> </li>
                                <li class="card-list-item"> <br/>    </li>
                                <li class="card-list-item">   </li>
                            </ul>
                        </div>
                        <button onClick={() => OpenForm()} class="card-button" className={isActive3 && activeClass || unactiveClass}> !</button>
                    </div>
                </div>
            </div>
            <div class="tarif-comment">
                    ?    <br/>    !
            </div>
            <div class="tarif-href">
                  
            </div>
        </div>        
    </div>
  );
}

export default Tarifs;

